@import-normalize;
body {
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 16px;
  line-height: 26px; }

a {
  border-bottom: 1px solid #9f23fb;
  color: #9f23fb;
  text-decoration: none;
  transition: all 150ms ease; }
  a:hover {
    background: #9f23fb;
    border-color: transparent;
    color: #fff; }

body {
  background: #fff; }

img {
  width: 100%;
  display: block; }

footer,
header,
main {
  width: 90%;
  max-width: 700px;
  margin: 0 auto; }

.footer__copyright {
  color: #777;
  margin-bottom: 40px;
  text-align: center; }

.gallery {
  margin-bottom: 20px; }

.gallery__item {
  display: block;
  margin-bottom: 10px;
  border: none; }

.header__logo {
  margin: 50px 0;
  padding-right: 20px; }

.not-found {
  display: none;
  font-size: 0.9em;
  font-style: italic;
  opacity: 0;
  transition: opacity 300ms ease-in;
  text-align: center; }
  .not-found.not-found--active {
    display: block;
    opacity: 1; }

.search {
  display: flex;
  margin-bottom: 20px; }

.search__button {
  background-image: url(./../images/search.gif);
  border: none;
  background-size: 52px;
  cursor: pointer;
  height: 52px;
  width: 52px; }

.search__field {
  animation-delay: 3;
  animation-direction: alternate;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-name: border-color-wave;
  animation-timing-function: linear;
  font-size: 16px;
  border-right: none;
  border: 2px solid #9f23fb;
  padding: 10px;
  width: calc(100% - 52px); }

@keyframes border-color-wave {
  from {
    border-color: #9f23fb; }
  to {
    border-color: #d92d86; } }
